
import React, { useState,Component } from 'react';
import './index.scss';
// import Search from '../../component/Search'; 

function Header() {
  let fullHeader = useState( true );
  let classForSmHeader="";
  let title="Welcome To Asset Hub!";
  let splitUrl=(window.location.href).split('/');
  
  if(splitUrl[splitUrl.length-1]!="" && (splitUrl[splitUrl.length-1]).indexOf('?')==-1){
    fullHeader = false;
    let name=splitUrl[splitUrl.length-1];
    title= name.charAt(0).toUpperCase() + name.slice(1);
    classForSmHeader=" ht-sm";

  }else if((splitUrl[splitUrl.length-1]).indexOf('?')!=-1){
    fullHeader = false;
    let name=splitUrl[splitUrl.length-2];
    title= name.charAt(0).toUpperCase() + name.slice(1);
    classForSmHeader=" ht-sm";
  }
  else{
    fullHeader = true;

  }  
  return (
      <div className="header">
          <div id="carouselExampleSlidesOnly" class="carousel slide" data-ride="carousel">
            <div className={'carousel-inner'+ classForSmHeader}>
              <div class="carousel-item active">
                <img className="d-block w-100" src={process.env.PUBLIC_URL + '/images/Banner-Image.png'} alt="North America Sales" />
                <div class="carousel-caption d-none d-md-block">                  
                  <div class="row">
                    <div class="col-md-2 text-left northAmericaLogo">
                      <img width="120px" src={process.env.PUBLIC_URL + '/images/North-America-Sales.png'} alt="North America Sales" />
                    </div>
                    <div class="heroText col-md-9 mt-5">                      
                      <h1>{title}</h1>
                      {fullHeader==true && <>
                      <p class="lead">Asset Hub is a catalog of North American, user-submitted assets aimed at <br />providing sellers with curated content at the click of a mouse.</p>
                          {/* <Search /> */}
                      </>}                     
                    </div>
                    <div class="col-md-1 text-right">
                      <img className="" width="30px" src={process.env.PUBLIC_URL + '/images/hamburger.svg'} />
                    </div>
                  </div>

                </div>
              </div>
              
            </div>
        </div>
      </div>
  );
}

export default Header;
