var hostname; var protocol; var port;
if (process.env.REACT_APP_ENV == "LOCAL") {
    // hostname="150.136.150.211";
    // port=":8001";
    // protocol="http:";
    hostname = "150.136.254.222";
    port = ":8443";
    protocol = "https:";
} else {
    hostname = window.location.hostname;
    protocol = window.location.protocol;
    port = ":8001"
}
export default {

    REACT_APP_IP: hostname,
    REACT_APP_P: protocol + '//',
    REACT_APP_Port: port


};